<template>
  <div class="index">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="btn-block">
        <a download  class="f-small main-c" href="../gy-files/template/教研组（备课组）考核导入模板.xls" >下载模板</a>
        <label for="importDom">
          <div class="btn-main btn-upload ml5">批量导入</div>
        </label>
        <input accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
               @change="upload($event)" ref="upload" id="importDom" type="file" hidden>
      </div>
      <div class="search-box">
        <div class="item">
          <span class="text">学期</span>
          <el-select @change="getDetail" class="term-input" v-model="query.termId">
            <el-option v-for="item in termList" :key="item.schoolTermId"
                       :label="item.termName" :value="item.schoolTermId"
            ></el-option>
          </el-select>
        </div>
        <div class="item">
          <span class="text">教研组</span>
          <el-select v-model="query.teachGroupId" @change="getDetail">
            <el-option v-for="item in teachGroup" :key="item.id"
                       :label="item.name" :value="item.id"
            ></el-option>
          </el-select>
        </div>
      </div>
      <div class="table-box">
        <div v-html="tableHTML"></div>
      </div>
    </main-part>
  </div>

</template>
<script>
  import mixins from '../../../../assets/js/mixins'
  export default {
    mixins: [mixins.$exportDetail,mixins.$spliceHTML],
    data() {
      return {
        nav: [{name: "教研组(备课组)考核", path: ""}],
        // 学期列表
        termList: [],
        // 教研组列表
        teachGroup: [],
        // 筛选条件
        query:{
          termId: null,
          teachGroupId: null,
        },
        tableHTML: ''
      }
    },
    methods: {
      // 获取学期
      async getTermList(){
        this.termList = await this.$ajax({
          url: '/school/querySchoolTermList.do'
        })
        this.termList.forEach(e=>{
          if(e.isDefault === 'YES'){
            this.query.termId = e.schoolTermId
          }
        })
      },
      // 获取教研组
      async getTeachGroup(){
        this.teachGroup = await this.$ajax({
          url: '/teachGroup/queryTeachGroupList.do'
        })
        this.query.teachGroupId = this.teachGroup[0].id
      },
      // 获取详情
      async getDetail(){
        let res = await this.$ajax({
          url: '/assessTeachGroup/getAssessTeachGroup.do',
          el: '.table-box',
          query: this.query
        })
        this.tableHTML = this.$spliceHTML(res)
      },

      // 导入
      async upload(e) {
        if (e.target.files.length === 0) return
        let file = e.target.files[0]
        let formData = new FormData()
        formData.append('file', file)
        let obj = {
          url: '/import/importAssessTeachGroup.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        this.$refs.upload.value = null;
        this.$remind('导入成功')
        this.getDetail()
      },
    },
    async mounted(){
      await this.getTermList()
      await this.getTeachGroup()
      this.getDetail()
    }
  }
</script>
